body {
  margin: 0px;
  background-color: rgb(31, 27, 27);
  background-repeat: no-repeat;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
h1 {
  margin: 0px;
  padding: 4px;
  height: 40px;
  background-color: rgb(255, 255, 255);
  overflow: hidden;
  transition: 1s;
}
h1:hover {
  height: 70px;
}
.three {
  font-weight: bolder;
}

p {
  margin-top: 140px;
  color: white;
  text-align: left;
  margin-left: 40px;
  line-height: 1.8;
  float: left;
  display: inline-flex;
  height: ;
  width: 50%;
  font-size: 20px;
  font-family: cursive;
}

img {
  display: flex;
  height: 400px;
  display: inline-flex;
  transition: 1s;
}
img:hover {
  transform: scaleX(1.03);
  margin-top: 10px;
}

.mode {
  display: inline-flex;
  background-color: white;
  border-radius: 8px;
  margin-left: 20px;
  padding: 20px;
}

.header {
  color: rgb(248, 244, 244);
  font-family: "Permanent Marker", cursive;
  text-transform: uppercase;
  position: fixed;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.349);
  backdrop-filter: blur(100px);
  top: 0;
  z-index: 1;
  width: 100%;
}
/* The progress container (grey background) */
.progress-container {
  width: 100%;
  height: 8px;
}
.progress-bar {
  height: 6px;
  background: #f5f500;
  width: 0%;
}
